import React, { useState, useEffect } from "react"
import { Share2, ChevronLeft } from 'lucide-react'
import { useNavigate } from 'react-router-dom'
import { createClient } from '@supabase/supabase-js'
import QRCode from 'qrcode'

const supabaseUrl = 'https://nuhekjuzkbvsidfemjfs.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Im51aGVranV6a2J2c2lkZmVtamZzIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzMzNDUwNTUsImV4cCI6MjA0ODkyMTA1NX0.RxusbIyrZwqS5s1ox4yLHTuUPIhwzJZYueuIQx60PDk'
const supabase = createClient(supabaseUrl, supabaseKey)

export default function ReceivePage() {
  const [walletAddress, setWalletAddress] = useState("")
  const [qrCode, setQrCode] = useState("")
  const navigate = useNavigate()

  useEffect(() => {
    const fetchWalletAddress = async () => {
      const userId = getTelegramUserId()
      if (userId) {
        const { data, error } = await supabase
          .from('seed_phrase')
          .select('trc_address')
          .eq('user_id', userId)
          .single()

        if (error) {
          console.error("Error fetching wallet address:", error)
        } else if (data) {
          setWalletAddress(data.trc_address)
          generateQRCode(data.trc_address)
        }
      }
    }

    fetchWalletAddress()
  }, [])

  const getTelegramUserId = () => {
    if (window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initDataUnsafe) {
      const user = window.Telegram.WebApp.initDataUnsafe.user
      return user ? user.id : null
    }
    return null
  }

  const generateQRCode = async (address) => {
    try {
      const qrCodeDataUrl = await QRCode.toDataURL(address, {
        width: 512, // Reduced from 1024
        margin: 0,    
        color: {
          dark: '#000000',
          light: '#ffffff'
        }
      })
      setQrCode(qrCodeDataUrl)
    } catch (err) {
      console.error("Error generating QR code:", err)
    }
  }

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(walletAddress)
    } catch (err) {
      console.error("Failed to copy:", err)
    }
  }

  const handleShare = async () => {
    try {
      await navigator.share({
        title: 'Wallet Address',
        text: walletAddress
      })
    } catch (err) {
      console.error("Failed to share:", err)
    }
  }

  const handleNavigateToMain = () => {
    navigate('/Wallet')
  }

  return (
    <div className="flex min-h-[100dvh] flex-col items-center bg-white px-4 pb-safe relative">
      <button
        onClick={handleNavigateToMain}
        className="absolute top-4 left-4 flex items-center text-blue-500 hover:text-blue-600 transition-colors"
      >
        <ChevronLeft className="w-5 h-5 mr-1" />
        
      </button>
      <div className="w-full max-w-md mt-12 flex flex-col">
        <h1 className="text-xl font-medium text-center mb-4"></h1>
        
        {/* Warning Message */}
        <div className="bg-orange-50 border-l-4 border-orange-400 p-3 mb-4 rounded-lg">
          <div className="flex">
            <div className="flex-shrink-0">
              
            </div>
            <div className="ml-3">
              <p className="text-sm text-orange-700">
                   Teather (USDT)   .
                    .
              </p>
            </div>
          </div>
        </div>

        {/* QR Code - reduced size */}
        <div className="bg-gray-100 w-full aspect-square mb-4 rounded-xl overflow-hidden max-w-[280px] mx-auto">
          {qrCode ? (
            <img src={qrCode} alt="QR Code" className="w-full h-full object-cover" />
          ) : (
            <div className="w-full h-full flex items-center justify-center">
              <span className="text-gray-500"> QR-...</span>
            </div>
          )}
        </div>

        {/* Action Buttons - moved higher */}
        <div className="mb-4 flex gap-3 justify-center">
          <button
            onClick={handleShare}
            className="p-3 rounded-xl bg-gray-100 hover:bg-gray-200 transition-colors"
          >
            <Share2 className="w-5 h-5 text-gray-600" />
          </button>
          <button
            onClick={handleCopy}
            className="flex-1 max-w-[200px] h-12 text-white font-medium rounded-xl bg-blue-500 hover:bg-blue-600 transition-colors"
          >
            
          </button>
        </div>

        {/* Wallet Address Section */}
        <div className="text-center mb-2 px-2">
          <p className="font-medium text-sm break-all mb-1">{walletAddress}</p>
          <p className="text-xs text-gray-500">
             QR-      
          </p>
        </div>
      </div>
    </div>
  )
}

