import { generateMnemonic, mnemonicToSeedSync } from "bip39"; //   
import HDKey from "hdkey";
import { keccak256 } from "ethereum-cryptography/keccak";
import bs58check from "bs58check";
import { ec as EC } from "elliptic";
// ,  crypto-browserify    
import crypto from 'crypto-browserify';

const ec = new EC("secp256k1");

function privateKeyToTronAddress(privateKey) {
  console.log("privateKeyToTronAddress: ", privateKey);  //   
  const key = ec.keyFromPrivate(privateKey);
  const publicKey = key.getPublic().encode("hex").slice(2);
  console.log("publicKey: ", publicKey);  //   
  const addressHash = keccak256(Buffer.from(publicKey, "hex")).slice(-20);
  console.log("addressHash: ", addressHash.toString("hex"));  //   
  const addressHex = Buffer.from(addressHash).toString("hex");
  const tronAddressHex = Buffer.from("41" + addressHex, "hex");
  const tronAddress = bs58check.encode(tronAddressHex);
  console.log("tronAddress: ", tronAddress);  //   
  return tronAddress;
}

export async function generateWallet() {
  try {
    console.log("Generating wallet...");
    const mnemonic = generateMnemonic();
    console.log("Mnemonic: ", mnemonic);  //  
    const seed = mnemonicToSeedSync(mnemonic);  //   
    console.log("Seed: ", seed.toString("hex"));  //  seed

    const root = HDKey.fromMasterSeed(Buffer.from(seed));
    console.log("Root: ", root);  //  root

    const addrnode = root.derive("m/44'/195'/0'/0/0");
    console.log("AddrNode: ", addrnode);  //  addrnode
    const privateKey = addrnode.privateKey.toString("hex");
    console.log("PrivateKey: ", privateKey);  //   

    const address = privateKeyToTronAddress(privateKey);
    console.log("Address: ", address);  //  

    return { mnemonic, privateKey, address };
  } catch (error) {
    console.error("Error generating wallet: ", error);  //  
  }
}
