import React, { useState, useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { ChevronLeft } from 'lucide-react'

export default function TransferPage() {
  const [address, setAddress] = useState("")
  const [amount, setAmount] = useState("")
  const [isFormValid, setIsFormValid] = useState(false)
  const navigate = useNavigate()

  useEffect(() => {
    setIsFormValid(address.trim() !== "" && amount.trim() !== "")
  }, [address, amount])

  const handlePaste = async () => {
    try {
      const text = await navigator.clipboard.readText()
      setAddress(text)
    } catch (err) {
      console.error("Failed to read clipboard:", err)
    }
  }

  const handleMaxAmount = () => {
    setAmount("1000")
  }

  const handleSubmit = () => {
    navigate('/Confirmation', { 
      state: { 
        amount: amount || "0,00",
        address 
      }
    })
  }

  const handleNavigateBack = () => {
    navigate(-1)
  }

  return (
    <div className="flex min-h-screen items-center justify-center bg-white p-4 relative">
      <button
        onClick={handleNavigateBack}
        className="absolute top-4 left-4 flex items-center text-blue-500 hover:text-blue-600 transition-colors"
      >
        <ChevronLeft className="w-5 h-5 mr-1" />
        
      </button>
      <div className="w-full max-w-md mx-auto bg-white rounded-xl shadow-md overflow-hidden">
        <div className="p-6">
          <h2 className="text-2xl font-medium text-center mb-6"> USDT</h2>
          <div className="space-y-6">
            <div className="space-y-2">
              <label className="text-sm text-gray-600">   </label>
              <div className="flex gap-2">
                <input
                  type="text"
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                  placeholder="  "
                  className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <button
                  onClick={handlePaste}
                  className="px-4 py-2 bg-white text-blue-500 border border-blue-500 rounded-md hover:bg-blue-50 focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  
                </button>
              </div>
            </div>

            <div className="space-y-2">
              <label className="text-sm text-gray-600"></label>
              <div className="flex gap-2">
                <input
                  type="number"
                  value={amount}
                  onChange={(e) => setAmount(e.target.value)}
                  placeholder="USDT "
                  className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <button
                  onClick={handleMaxAmount}
                  className="px-4 py-2 bg-white text-blue-500 border border-blue-500 rounded-md hover:bg-blue-50 focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  
                </button>
              </div>
              <div className="text-sm text-gray-500">= {amount || "0,00"}$</div>
            </div>

            <button 
              onClick={handleSubmit}
              disabled={!isFormValid}
              className={`w-full h-12 text-lg mt-8 text-white font-medium rounded-xl bg-gradient-to-r from-blue-500 to-blue-600 hover:from-blue-600 hover:to-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 ${
                !isFormValid ? 'opacity-50 cursor-not-allowed' : ''
              }`}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

