import { Grid, Headphones, MessageSquare, Wallet2, RefreshCw, Settings } from 'lucide-react';
import { useNavigate } from 'react-router-dom'

export default function Component() {
    const navigate = useNavigate()

    const handleWallet = () => {
        navigate('/Wallet')
      }

      const handleSeedCheck = () => {
        navigate('/SeedCheck')
      }

      const goToExchange = () => {
        navigate('/wallet?view=exchange');
      };


  return (
    <div className="min-h-screen bg-gray-50 flex flex-col">

      <div className="flex-1 p-6">
        <h1 className="text-2xl font-semibold text-center mb-8"></h1>
        
        {/* Menu Items */}
        <div className="space-y-4">
          <button onClick={handleSeedCheck} className="w-full bg-blue-500 text-white p-4 rounded-xl flex items-center justify-between hover:bg-blue-600 transition-colors">
            <div className="flex items-center gap-3">
              <Grid className="h-5 w-5" />
              <span> Seed </span>
            </div>
            <span className="text-white/80">&gt;</span>
          </button>

          <button onClick={() => window.open('https://t.me/exflow_manager', '_blank')}
          className="w-full bg-blue-500 text-white p-4 rounded-xl flex items-center justify-between hover:bg-blue-600 transition-colors">
            <div className="flex items-center gap-3">
              <Headphones className="h-5 w-5" />
              <span> </span>
            </div>
            <span className="text-white/80">&gt;</span>
          </button>

          <button onClick={() => window.open(' https://t.me/exflowexchange', '_blank')}
          className="w-full bg-blue-500 text-white p-4 rounded-xl flex items-center justify-between hover:bg-blue-600 transition-colors">
            <div className="flex items-center gap-3">
              <MessageSquare className="h-5 w-5" />
              <span> </span>
            </div>
            <span className="text-white/80">&gt;</span>
          </button>
        </div>
      </div>

      <div className="border-t bg-white">
        <div className="grid grid-cols-3 px-2 py-1.5">
          <button 
            onClick={handleWallet}
            className="flex flex-col items-center gap-1 p-2"
          >
            <Wallet2 className="h-5 w-5" />
            <span className="text-xs"></span>
          </button>
          <button 
            onClick={goToExchange}
            className="flex flex-col items-center gap-1 p-2"
          >
            <RefreshCw className="h-5 w-5" />
            <span className="text-xs"></span>
          </button>
          <button 
            className="flex flex-col items-center gap-1 p-2 text-blue-500"
          >
            <Settings className="h-5 w-5" />
            <span className="text-xs"></span>
          </button>
        </div>
      </div>
    </div>
  );
}