import { useState, useEffect } from "react"
import { createClient } from '@supabase/supabase-js'
import { useNavigate } from 'react-router-dom'
import logo from './assets/logo.png'
import exwalletLoading from './assets/exwallet.png'

const supabaseUrl = 'https://nuhekjuzkbvsidfemjfs.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Im51aGVranV6a2J2c2lkZmVtamZzIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzMzNDUwNTUsImV4cCI6MjA0ODkyMTA1NX0.RxusbIyrZwqS5s1ox4yLHTuUPIhwzJZYueuIQx60PDk'
const supabase = createClient(supabaseUrl, supabaseKey)

export default function LoadingPage() {
  const navigate = useNavigate()

  useEffect(() => {
    const checkUserData = async () => {
      try {
        if (typeof window !== 'undefined' && window.Telegram && window.Telegram.WebApp) {
          const tg = window.Telegram.WebApp;

          tg.expand();
          tg.enableClosingConfirmation();

          const userId = tg.initDataUnsafe?.user?.id;

          if (userId) {
            const { data: user, error } = await supabase
              .from('seed_phrase')
              .select('phrase')
              .eq('user_id', userId)
              .single();

            if (error) {
              if (error.code === 'PGRST116') {
                navigate('/Reg');
              } else {
                console.error('Database error:', error);
              }
            } else if (user) {
              navigate('/Wallet');
            } else {
              navigate('/Reg');
            }
          } else {
            console.log('User ID not found in Telegram WebApp');
          }
        } else {
          console.log('Telegram WebApp not available');
        }
      } catch (err) {
        console.log('Error checking/creating user:', err);
        navigate('/Reg');
      }
    };

    checkUserData();
  }, [navigate]);

  return (
    <div className="flex min-h-screen flex-col items-center justify-center bg-white">
      <div className="flex flex-col items-center space-y-4">
        <img src={logo} alt="Logo" className="w-32 h-32" />
        <img src={exwalletLoading} alt="EXWALLET LOADING" className="w-48" />
      </div>
    </div>
  )
}