import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom'
import { ChevronLeft } from 'lucide-react';

export default function Component() {
  const [step, setStep] = useState('input');
  const [amount, setAmount] = useState('');
  const [receiveAmount, setReceiveAmount] = useState('');
  const [currency, setCurrency] = useState('RUB');
  const [city, setCity] = useState('');
  const [userId, setUserId] = useState(null);
  const [userName, setUserName] = useState(null);
  const [isFormValid, setIsFormValid] = useState(false);
  const navigate = useNavigate()

  const exchangeRates = {
    USD: 1,
    USDT: 1,
    EUR: 1.0562,
    RUB: 0.009572,
  };

  useEffect(() => {
    if (!isNaN(parseFloat(amount)) && currency in exchangeRates) {
      const calculatedAmount = parseFloat(amount) * exchangeRates[currency];
      setReceiveAmount(calculatedAmount.toFixed(2));
    } else {
      setReceiveAmount('');
    }
  }, [amount, currency]);

  useEffect(() => {
    const user = window.Telegram.WebApp.initDataUnsafe.user;
    if (user) {
      setUserId(user.id);
      setUserName(user ? user.username : null)
    }
  }, []);

  useEffect(() => {
    setIsFormValid(amount !== '' && city !== '' && receiveAmount !== '');
  }, [amount, city, receiveAmount]);

  const sendToTelegram = async () => {
    const token = '7903293682:AAHpttIDb071Ed2THA4l--eK_BeldcKA2Ho';
    const chatId = '7033650933';
  
    const message = `
   <b> :</b>
  - <b>:</b> ${amount} ${currency}
  - <b>:</b> ${receiveAmount} USDT
  - <b>:</b> ${city}
  - <b>:</b> @${userName} // <a href="tg://user?id=${userId}"></a>
    `;
  
    try {
      const response = await fetch(`https://api.telegram.org/bot${token}/sendMessage`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          chat_id: chatId,
          text: message,
          parse_mode: 'HTML', 
        }),
      });
  
      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(errorText);
      }
  
    } catch (error) {
      console.log("", error)
    }
  };

  const handleNext = () => {
    if (step === 'input') setStep('confirm');
    else if (step === 'confirm') {
      setStep('processing');
      sendToTelegram(); 
    }
  };

  const handleBack = () => {
    if (step === 'confirm') setStep('input');
  };

  const handleNavigateToMain = () => {
    navigate('/Wallet')
  }

  return (
    <div className="min-h-screen bg-white flex flex-col items-center justify-center p-4 relative">
      <button
        onClick={handleNavigateToMain}
        className="absolute top-4 left-4 flex items-center text-blue-500 hover:text-blue-600 transition-colors"
      >
        <ChevronLeft className="w-5 h-5 mr-1" />
        
      </button>
      <div className="w-full max-w-md bg-white rounded-xl shadow-xl p-6">
        {/* Progress Bar */}
        <div className="flex items-center justify-between mb-8">
          <div className="w-full flex items-center">
            <div className="w-3 h-3 rounded-full bg-blue-500" />
            <div className={`flex-1 h-1 mx-2 ${step !== 'input' ? 'bg-blue-500' : 'bg-gray-200'}`} />
            <div className={`w-3 h-3 rounded-full ${step !== 'input' ? 'bg-blue-500' : 'bg-gray-200'}`} />
            <div className={`flex-1 h-1 mx-2 ${step === 'processing' ? 'bg-blue-500' : 'bg-gray-200'}`} />
            <div className={`w-3 h-3 rounded-full ${step === 'processing' ? 'bg-blue-500' : 'bg-gray-200'}`} />
          </div>
        </div>

        {/* Title */}
        <h1 className="text-xl font-semibold mb-6">
          {step === 'input' && '    '}
          {step === 'confirm' && ' '}
          {step === 'processing' && '   !'}
        </h1>

        {/* Content */}
        <div className="space-y-4">
          <div className="bg-gray-50 rounded-lg p-4">
            <div className="text-sm text-gray-500 mb-2"> </div>
            <div className="flex items-center justify-between">
              <select
                className="bg-transparent font-medium focus:outline-none"
                value={currency}
                onChange={(e) => setCurrency(e.target.value)}
                disabled={step !== 'input'}
              >
                <option value="RUB">RUB</option>
                <option value="EUR">EUR</option>
                <option value="USD">USD</option>
                <option value="USDT">USDT</option>
              </select>
              <input
                type="text"
                pattern="\d*"
                value={amount}
                onChange={(e) => {
                  const value = e.target.value.replace(/\D/g, '');
                  setAmount(value);
                }}
                placeholder="0"
                className="text-right font-medium bg-transparent focus:outline-none"
                disabled={step !== 'input'}
              />
            </div>
          </div>

          <div className="bg-gray-50 rounded-lg p-4">
            <div className="text-sm text-gray-500 mb-2"> </div>
            <div className="flex items-center justify-between">
              <div className="font-medium">USDT</div>
              <div className="font-medium">{receiveAmount}</div>
            </div>
          </div>

          {step === 'input' && (
            <div className="bg-gray-50 rounded-lg p-4">
              <div className="text-sm text-gray-500 mb-2"></div>
              <div className="flex items-center justify-between">
                <input
                  type="text"
                  value={city}
                  onChange={(e) => setCity(e.target.value)}
                  placeholder=" "
                  className="w-full font-medium bg-transparent focus:outline-none"
                />
              </div>
            </div>
          )}
        </div>

        {/* Action Buttons */}
        <div className="mt-6 flex space-x-4">
          {step === 'confirm' && (
            <button
              onClick={handleBack}
              className="flex-1 bg-gray-200 text-gray-800 py-3 rounded-lg font-medium hover:bg-gray-300 transition-colors"
            >
              
            </button>
          )}
          {step !== 'processing' ? (
            <button
              onClick={handleNext}
              disabled={step === 'input' && !isFormValid}
              className={`${
                step === 'confirm' ? 'flex-1' : 'w-full'
              } bg-blue-500 text-white py-3 rounded-lg font-medium hover:bg-blue-600 transition-colors ${
                step === 'input' && !isFormValid ? 'opacity-50 cursor-not-allowed' : ''
              }`}
            >
              {step === 'input' ? '' : ''}
            </button>
          ) : (
            <button 
              onClick={() => window.open('https://t.me/exflow_manager', '_blank')}
              className="w-full bg-blue-500 text-white py-3 rounded-lg font-medium hover:bg-blue-600 transition-colors"
            >
               
            </button>
          )}
        </div>
      </div>
    </div>
  );
}