import React, { useState, useEffect } from "react"
import { useLocation, useNavigate } from "react-router-dom"
import { ChevronLeft, AlertCircle } from 'lucide-react'
import './index.css'

export default function ConfirmationPage() {
  const location = useLocation()
  const navigate = useNavigate()
  const { amount = "0,00", address = "" } = location.state || {}
  const [showNotification, setShowNotification] = useState(false)
  const [notificationMessage, setNotificationMessage] = useState("")

  const showToast = (message) => {
    setNotificationMessage(message)
    setShowNotification(true)
    setTimeout(() => setShowNotification(false), 5000)
  }

  const handleConfirm = () => {
    // Simulating an error for demonstration
    showToast("  , ,    !")
  }

  const handleNavigateBack = () => {
    navigate(-1)
  }

  return (
    <div className="flex min-h-screen flex-col bg-white p-4 relative">
      {showNotification && (
        <div className="fixed top-0 left-0 right-0 z-50 p-4 bg-red-500 text-white transition-all duration-300 ease-in-out">
          <div className="flex items-center">
            <AlertCircle className="w-5 h-5 mr-2" />
            <p>{notificationMessage}</p>
          </div>
        </div>
      )}
      <button
        onClick={handleNavigateBack}
        className="absolute top-4 left-4 flex items-center text-blue-500 hover:text-blue-600 transition-colors"
      >
        <ChevronLeft className="w-5 h-5 mr-1" />
        
      </button>
      <div className="w-full max-w-md mx-auto mt-12">
        <div className="text-center mb-6">
          <h2 className="text-2xl font-medium mb-2"></h2>
          <div className="text-2xl font-medium mb-1">-{amount} USDT</div>
          <div className="text-sm text-gray-500">= {amount} $</div>
        </div>

        <div className="bg-white rounded-xl shadow-md overflow-hidden mb-4">
          <div className="p-4 space-y-4">
            <div className="flex justify-between items-center">
              <span className="text-gray-600"></span>
              <div className="flex items-center">
                <span className="mr-2">featherusdt (USDT)</span>
                <span className="text-gray-400"></span>
              </div>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-gray-600"></span>
              <span className="text-gray-400"></span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-gray-600"></span>
              <span className="text-gray-400"></span>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-md overflow-hidden mb-6">
          <div className="p-4 space-y-4">
            <div className="flex justify-between items-center">
              <span className="text-gray-600"> </span>
              <span>0.00 USDT</span>
            </div>
            <div className="flex justify-between items-center font-medium">
              <span></span>
              <span>{amount} $</span>
            </div>
          </div>
        </div>

        <button 
          className="w-full h-12 text-lg text-white font-medium rounded-xl bg-blue-500 hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
          onClick={handleConfirm}
        >
          
        </button>
      </div>
    </div>
  )
}

