"use client"

import { useState, useEffect } from "react"
import { Settings, RefreshCw, Wallet2 } from 'lucide-react'
import { useNavigate, useLocation } from 'react-router-dom'
import { createClient } from '@supabase/supabase-js'
import USDTIcon from "./assets/usdt.svg"
import TronIcon from "./assets/tron.svg"
import SendIcon from "./assets/send.png"
import ReceiveIcon from "./assets/get.png"
import './index.css'

const supabaseUrl = 'https://nuhekjuzkbvsidfemjfs.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Im51aGVranV6a2J2c2lkZmVtamZzIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzMzNDUwNTUsImV4cCI6MjA0ODkyMTA1NX0.RxusbIyrZwqS5s1ox4yLHTuUPIhwzJZYueuIQx60PDk'
const supabase = createClient(supabaseUrl, supabaseKey)

export default function Component() {
  const [currentView, setCurrentView] = useState("wallet")
  const navigate = useNavigate()
  const location = useLocation()
  const [userAvatarUrl, setUserAvatarUrl] = useState(null);
  const [userFirstName, setUserFirstName] = useState("")

  useEffect(() => {
    const params = new URLSearchParams(location.search)
    const view = params.get("view")
    if (view) {
      setCurrentView(view)
    }

    const avatarUrl = getTelegramUserAvatar()
    setUserAvatarUrl(avatarUrl)

    const firstName = getTelegramUserFirstName()
    setUserFirstName(firstName)

  }, [location.search])

  const getTelegramUserAvatar = () => {
    if (window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initDataUnsafe) {
      const user = window.Telegram.WebApp.initDataUnsafe.user
      return user ? user.photo_url : null
    }
    return null
  }

  const getTelegramUserFirstName = () => {
    if (window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initDataUnsafe) {
      const user = window.Telegram.WebApp.initDataUnsafe.user
      return user ? user.first_name : "User"
    }
    return "User"
  }

  const handTransferclick = () => {
    navigate('/Transfer')
  }

  const handSendClick = () => {
    navigate('/Send')
  }

  const handSettingsferclick = () => {
    setCurrentView("settings")
    navigate('/SettingsPage')
  }

  const handReceiveClick = () => {
    navigate('/Receive')
  }

  return (
    <div className="flex min-h-screen flex-col bg-white">
      {currentView === "wallet" ? (
        <div className="flex-1">
          <div className="p-3">
            <div className="flex items-center gap-3">
              {userAvatarUrl ? (
                  <img src={userAvatarUrl} alt="User Avatar" className="h-8 w-8 rounded-full object-cover" />
                ) : (
                  <div className="h-8 w-8 rounded-full bg-gray-100" />
                )}
              <h2 className="text-base">, {userFirstName}</h2>
            </div>
            <div className="mt-4 space-y-0.5 text-center">
              <div className="text-2xl font-medium">0,00 USDT</div>
              <div className="text-sm text-gray-500">$ 0,00 +0,00%</div>
            </div>
            <div className="mt-6 grid grid-cols-2 gap-3">
              <button onClick={handSendClick} className="flex h-11 items-center justify-center gap-2 rounded-xl bg-blue-500 text-white transition-colors active:bg-blue-700">
                <img src={SendIcon} alt="Send" className="h-5 w-5" />
                
              </button>
              <button onClick={handReceiveClick} className="flex h-11 items-center justify-center gap-2 rounded-xl bg-blue-500 text-white transition-colors active:bg-blue-700">
                <img src={ReceiveIcon} alt="Receive" className="h-5 w-5" />
                
              </button>
            </div>
            <div className="mt-4">
              <h3 className="mb-2 text-sm font-medium">Tokens</h3>
              <div className="space-y-2">
                <div className="flex items-center gap-3 rounded-2xl border p-2.5">
                  <div className="flex h-8 w-8 items-center justify-center">
                    <img
                      src={USDTIcon}
                      alt="USDT TRC-20"
                      className="w-8 h-8"
                    />
                  </div>
                  <div className="flex-1">
                    <div className="text-sm font-medium">USDT TRC-20</div>
                    <div className="text-xs text-gray-500">$ 0,00 +0,00%</div>
                  </div>
                  <div className="text-right">
                    <div className="text-sm font-medium">0,00 USDT</div>
                  </div>
                </div>
                <div className="flex items-center gap-3 rounded-2xl border p-2.5">
                  <div className="flex h-8 w-8 items-center justify-center">
                    <img
                      src={TronIcon}
                      alt="TRON"
                      className="w-8 h-8"
                    />
                  </div>
                  <div className="flex-1">
                    <div className="text-sm font-medium">TRON</div>
                    <div className="text-xs text-gray-500">$ 0,00 +0,00%</div>
                  </div>
                  <div className="text-right">
                    <div className="text-sm font-medium">0,00 TRON</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : currentView === "exchange" ? (
        <div className="flex-1 p-4">
          <div className="flex flex-col items-center">
            <div className="flex gap-2 mb-8">
              {[...Array(4)].map((_, i) => (
                <div key={i} className="w-2 h-2 rounded-full bg-blue-500" />
              ))}
            </div>
            <h1 className="text-xl font-medium mb-2">  USDT</h1>
            <p className="text-sm text-gray-500 mb-8">    </p>
            <div className="space-y-4 text-sm mb-8">
              <p>
                     USDT,   USDT     EXWALLET.
                    .
              </p>
              <p>
                  USDT       .    
                        .
              </p>
              <p>
                      ,    
                  
              </p>
            </div>
            <div className="mt-auto w-full space-y-4">
              <p className="text-xs text-gray-500 text-center">
                        
              </p>
              <button onClick={handTransferclick} className="w-full py-3 bg-blue-500 text-white rounded-xl">
                
              </button>
            </div>
          </div>
        </div>
      ) : (
        <div className="flex-1 p-4">
          <h1></h1>
        </div>
      )}
      <div className="border-t bg-white">
        <div className="grid grid-cols-3 px-2 py-1.5">
          <button 
            onClick={() => setCurrentView("wallet")}
            className={`flex flex-col items-center gap-1 p-2 ${currentView === "wallet" ? "text-blue-500" : ""}`}
          >
            <Wallet2 className="h-5 w-5" />
            <span className="text-xs"></span>
          </button>
          <button 
            onClick={() => setCurrentView("exchange")}
            className={`flex flex-col items-center gap-1 p-2 ${currentView === "exchange" ? "text-blue-500" : ""}`}
          >
            <RefreshCw className="h-5 w-5" />
            <span className="text-xs"></span>
          </button>
          <button 
            onClick={handSettingsferclick}
            className={`flex flex-col items-center gap-1 p-2 ${currentView === "settings" ? "text-blue-500" : ""}`}
          >
            <Settings className="h-5 w-5" />
            <span className="text-xs"></span>
          </button>
        </div>
      </div>
    </div>
  )
}

