import React, { useState, useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { ClipboardCopy } from 'lucide-react'
import { createClient } from '@supabase/supabase-js'
import logo from './assets/logo.png'
import exwalletLoading from './assets/exwallet.png'

const supabaseUrl = 'https://nuhekjuzkbvsidfemjfs.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Im51aGVranV6a2J2c2lkZmVtamZzIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzMzNDUwNTUsImV4cCI6MjA0ODkyMTA1NX0.RxusbIyrZwqS5s1ox4yLHTuUPIhwzJZYueuIQx60PDk'
const supabase = createClient(supabaseUrl, supabaseKey)

export default function SeedPhraseDisplay() {
  const [seedPhrase, setSeedPhrase] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState(null)
  const navigate = useNavigate()

  useEffect(() => {
    const fetchSeedPhrase = async (userId) => {
      try {
        const { data, error } = await supabase
          .from('seed_phrase')
          .select('phrase')
          .eq('user_id', userId)
          .single()

        if (error) throw error

        if (data) {
          setSeedPhrase(data.phrase)
        } else {
          setError("Seed phrase not found")
        }
      } catch (error) {
        console.error("Error fetching seed phrase:", error)
        setError("Failed to fetch seed phrase")
      } finally {
        setIsLoading(false)
      }
    }

    if (typeof window !== 'undefined' && window.Telegram && window.Telegram.WebApp) {
      const tg = window.Telegram.WebApp
      const user = tg.initDataUnsafe?.user

      if (user && user.id) {
        const userId = user.id.toString()
        fetchSeedPhrase(userId)
      } else {
        setError("User ID not found")
        setIsLoading(false)
      }
    } else {
      setError("Telegram WebApp not available")
      setIsLoading(false)
    }
  }, [])

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(setSeedPhrase)
      navigate('/SeedCheckNext')
    } catch (err) {
      console.error("Failed to copy:", err)
    }
  }


  if (isLoading) {
    return (
      <div className="flex min-h-screen flex-col items-center justify-center bg-white">
      <div className="flex flex-col items-center space-y-4">
        <img src={logo} alt="Logo" className="w-32 h-32" />
        <img src={exwalletLoading} alt="EXWALLET LOADING" className="w-48" />
      </div>
    </div>
    )
  }

  if (error) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <p className="text-red-500">{error}</p>
      </div>
    )
  }

  return (
    <div className="flex min-h-screen flex-col items-center justify-center bg-white p-4">
      <div className="w-full max-w-md flex flex-col items-center px-4">
        <h1 className="text-2xl font-medium text-center mb-8"> Seed </h1>
        
        <p className="text-sm text-gray-500 mb-4">
           seed - 
        </p>

        <div 
          onClick={handleCopy}
          className="w-full bg-gray-100 p-4 rounded-xl mb-2 min-h-[100px] flex items-center justify-center text-center cursor-pointer"
        >
          {seedPhrase}
        </div>

        <div className="flex items-center gap-2 text-sm text-gray-500 mb-8">
          <ClipboardCopy className="w-4 h-4" />
          <span>  </span>
        </div>

        <button 
          onClick={() => navigate('/wallet')}
          className="w-full max-w-[200px] h-12 text-white font-medium rounded-xl bg-blue-500 hover:bg-blue-600 transition-colors"
        >
          
        </button>
      </div>
    </div>
  )
}